import { useState } from "react";
import { Result } from "./Result";
import { Clock } from "./Clock";
import { useApiRatesData } from "./useApiRatesData";
import { Loader } from "./animation";
import {
    StyledForm,
    Header,
    Fieldset,
    Legend,
    Field,
    Select,
    LabelText,
    Button,
    Paragraph,
    Label,
    Loading,
    Failed,
} from "./styled.js";

export const Form = () => {
    const [result, setResult] = useState();
    const ratesData = useApiRatesData();

    const calculateResult = (currency, amount) => {
        const rate = ratesData.rates[currency];

        setResult({
            sourceAmount: +amount,
            targetAmount: amount * rate,
            currency,
        });
    }

    const [currency, setCurrency] = useState("EUR");
    const [amount, setAmount] = useState("");

    const onSubmit = (event) => {
        event.preventDefault();
        calculateResult(currency, amount);
    }

    return (
        <StyledForm onSubmit={onSubmit}>
            <Header>
                Konwerter kurs√≥w walutowych
            </Header>
            {ratesData.state === "loading"
                ? (
                    <Loading>
                        Momencik... <br /> Trwa proces ≈Çadowania kurs√≥w walutowych... <br /> <Loader />
                    </Loading>
                )
                : (
                    ratesData.state === "error" ? (
                        <Failed>
                            WyglƒÖda na to, ≈ºe co≈õ posz≈Ço nie tak. Upewnij siƒô, ≈ºe masz po≈ÇƒÖczenie z internetem :)
                        </Failed>
                    ) : (
                        <>
                            <Clock />
                            <Fieldset>
                                <Legend>
                                    Konwerter walut
                                </Legend>
                                <Paragraph>
                                    <strong>
                                        Pola oznaczone * muszƒÖ byƒá wype≈Çnione,&nbsp;
                                    </strong>
                                </Paragraph>
                                <Paragraph>
                                    <Label>
                                        <LabelText>
                                            Kwota w PLN*:
                                        </LabelText>
                                        <Field
                                            value={amount}
                                            onChange={({ target }) => setAmount(target.value)}
                                            required
                                            type="number"
                                            step="0.01"
                                            placeholder="Podaj porzƒÖdanƒÖ kwotƒô w z≈Ç"
                                        />
                                    </Label>
                                </Paragraph>
                                <Paragraph>
                                    <Label>
                                        <LabelText>
                                            Konwertujƒô walutƒô na:
                                        </LabelText>
                                        <Select
                                            as="select"
                                            value={currency}
                                            onChange={({ target }) => setCurrency(target.value)}
                                        >
                                            {Object.keys(ratesData.rates).map((currency => (
                                                <option
                                                    key={currency}
                                                    value={currency}
                                                >
                                                    {currency}
                                                </option>
                                            )))}
                                        </Select>
                                    </Label>
                                </Paragraph>
                                <Paragraph>
                                    <Button>Hit me</Button>
                                </Paragraph>

                                <Result result={result} />
                            </Fieldset>
                        </>
                    )
                )}
        </StyledForm>
    );
};

export default Form;